<template>
    <div class="city_body">
        <div class="city_list">
            <Loading v-if="isLoading"></Loading>
            <scroller ref="city">
                <div>
                    <div class="city_hot">
                        <h2>热门城市</h2>
                    <ul class="clearfix">
                        <li v-for="(item, i) in CityHot" :key="item.i" @tap="ChangeCity(item, i)">{{item}}</li>
                    </ul>
                    </div>
                    <div class="city_sort"  ref="city_h2">
                        <div v-for="item in CityList" :key="item.id">
                            <h2>{{item.index}}</h2>
                        <ul>
                            <li v-for="itemlist in item.list" :key="itemlist.id" @tap="ChangeCity(itemlist.nm, itemlist.id)">{{itemlist.nm}}</li>
                            <!-- <li>鞍山</li>
                            <li>安庆</li>
                            <li>安阳</li> -->
                        </ul>
                        </div>	
                    </div>
                </div>
            </scroller>
        </div>
        <div class="city_index">
            <ul>
                <li v-for="(item, i) in CityList" :key="item.index" @touchstart = "handleToIndex(i)" @tap="ChangeCity(item.nm, item.id)">{{item.index}}</li>
                <!-- <li>B</li>
                <li>C</li>
                <li>D</li>
                <li>E</li> -->
            </ul>
        </div>
    </div>
</template>
<script>
export default {
    name: "citylist",
    data: function(){
        return {
            CityList: [],
            CityHot: [],
            isLoading: true,
        }
    },
    created: function(){
        var _this = this;
        var CityList = window.localStorage.getItem("cityList");
        var CityHot = window.localStorage.getItem("cityHot");
        if(CityList && CityHot){
            this.CityList = JSON.parse(CityList);
            this.CityHot = JSON.parse(CityHot);
            this.isLoading = false;
        }
        else{
            this.$http.get("http://39.97.33.178/api/cityList").then(function(res){
            // console.log(res.data.status);
                if(res.data.status === 0){
                    // 基础的数组的格式  [{index: "A", list: [{nm : "阿坝", id: 123]]
                    var cities = res.data.data.cities;
                    // console.log(cities);
                    _this.isLoading = false;
                    
                    var { cityList, cityHot } = _this.formatCityList(cities);
                    
                    _this.CityList = cityList;
                    _this.CityHot = cityHot;
                    // console.log(_this.CityList);
                    
                    
                    // console.log(_this.CityList);
                    //  将数据存储到本地存储中去
                    window.localStorage.setItem("cityList", JSON.stringify(cityList));
                    window.localStorage.setItem("cityHot", JSON.stringify(cityHot));
                    
                }
            
            })
        }

        
    },
    methods: {
        formatCityList: function(cities){
            var cityList = [];
            var cityHot = [];
            
            //  将isHot 为1的项添加到热门城市数组中
            for(var i=0;i<cities.length;i++){
                if(cities[i].isHot === 1){
                    cityHot.push(cities[i].nm)
                }
            }

            for(var i=0;i<cities.length;i++){
                var firstLetter = cities[i].py.substring(0, 1).toUpperCase();
                
                // 进行判断，如果在结果中的话执行else， 进行一个添加的操作；如果不再结果集中的话，进行一个添加
                
                if(inList(firstLetter)){
                    // 不在列表中执行添加的操作
                    cityList.push({ index: firstLetter, list: [{nm: cities[i].nm, id: cities[i].id}] });
                }
                else{ // 在列表中
                    //使用some进行循环，如果在列表中的话，就找到索引值index + 1 
                    cityList.some(function(item){
                     
                    
                        if(item.index === firstLetter){
                            item.list.push({ nm: cities[i].nm, id: cities[i].id });
                            return true;
                        }
                    })
                }
            }

            cityList.sort(function(a, b){
                // 正序 排序
                if(a.index < b.index){
                    return -1; 
                }
                else if(a.index > b.index){
                    return 1;
                }
                else{
                    return 0;
                }
            })

            function inList(firstLetter){
                
                for(var i=0;i<cityList.length;i++){
                    
                    if(cityList[i].index === firstLetter){
                        return false;
                    }
                }
                    return true;
            }

            // console.log(cityList);
            // console.log(cityHot);
            // this.CityList = cityList;
            // this.CityHot = cityHot;
            return {
                cityList,
                cityHot,
            }

            
        },
        handleToIndex: function(i){
            var h2 = this.$refs.city_h2.getElementsByTagName("h2");// 获取所有的标签
            // 当使用了better-scroll(scroller)之后就没办法使用原生的来进行跳转了,可以使用better-scroll 中提供的 scroll-to 来实现点击的跳转
            // this.$refs.city_h2.parentNode.scrollTop = h2[i].offsetTop;

            // 因为将ref 属性用在了一个组件标签中的时候，就可以拿到整个组件，所以对应的也可以拿到组件的方法
            //  注意：点击字母之后的 y 值变化是负值，所以需要加上负号
            this.$refs.city.toScrollTop(-h2[i].offsetTop)
        },
        ChangeCity: function(nm, id){
            this.$store.commit("city/CITY_CHANGE", { nm, id });
            // 将修改之后的数据存储到本地存储中
            window.localStorage.setItem("nowNM", nm);
            window.localStorage.setItem("nowID", id);
            // console.log(this.CityHot);
            // 使用编程式导航将路由变到相应的城市的页面
            this.$router.push("/movie/nowplaying");
            // this.$store.commit("city/")


        }
    }
}
</script>
<style lang="scss" scoped>
    #content .city_body{ margin-top: 45px; display: flex; width:100%; position: absolute; top: 0; bottom: 0;}
    .city_body .city_list{ flex:1; overflow: auto; background: #FFF5F0;}
    .city_body .city_list::-webkit-scrollbar{
        background-color:transparent;
        width:0;
    }
    .city_body .city_hot{ margin-top: 20px;}
    .city_body .city_hot h2{ padding-left: 15px; line-height: 30px; font-size: 14px; background:#F0F0F0; font-weight: normal;}
    .city_body .city_hot ul li{ float: left; background: #fff; width: 29%; height: 33px; margin-top: 15px; margin-left: 3%; padding: 0 4px; border: 1px solid #e6e6e6; border-radius: 3px; line-height: 33px; text-align: center; box-sizing: border-box;}
    .city_body .city_sort div{ margin-top: 20px;}
    .city_body .city_sort h2{ padding-left: 15px; line-height: 30px; font-size: 14px; background:#F0F0F0; font-weight: normal;}
    .city_body .city_sort ul{ padding-left: 10px; margin-top: 10px;}
    .city_body .city_sort ul li{ line-height: 30px; line-height: 30px;}
    .city_body .city_index{ width:20px; display: flex; flex-direction:column; justify-content:center; text-align: center; border-left:1px #e6e6e6 solid;}
</style>
